export const baserowClient = new AxiosClient(axiosConfigurator);

export type baserowType<T> = {
    count: number;
    next: string | null;
    previous: string | null;
    results: T[];
}

export type baserowNestedType = {
    id: number;
    value: string;
    order: string;
}

export type baserowListType = {
    "id": number,
    "value": string,
    "color": string
}

export type QuestionsType = {
    id: number;
    order: string;
    Title: string;
    Actif: boolean;
    Answers: baserowNestedType & {"Next Question Id":baserowNestedType[]}[];
    Type: baserowListType;
    Description: string | null;
    "Next Question": baserowNestedType[];
};

export type ServicesType = {
    id: number;
    order: string;
    Title: string;
    Description: string | null;
    Rooms: baserowNestedType[];
    "First Question": baserowNestedType[];
    Actif: boolean;
    Image: string | null;
};

export type RoomsType = {
    id: number;
    order: string;
    Title: string;
    DefaultSize: string | null;
    Services: baserowNestedType[];
    Image: string | null;
};
